/** @jsx jsx */
import { jsx } from "theme-ui";
import { graphql } from "gatsby";
import React from "react";
import CollectionListingContainer from "@components/CollectionListingContainer";
import SEO from "@components/seo";

const InsightsFor = (props) => {
  const { category } = props.data.craft;

  return (
    <React.Fragment>
      <SEO
        metadata={{
          metaTitle: category.title,
        }}
        location={props.location}
      />
      <CollectionListingContainer category={category} listing="industries" />
    </React.Fragment>
  );
};

export default InsightsFor;

export const query = graphql`
  query IndustriesQuery($id: Craft_QueryArgument) {
    craft {
      category(id: [$id]) {
        ... on Craft_industries_Category {
          title
        }
      }
    }
  }
`;
